





























import { Component, Vue  } from 'vue-property-decorator';
import api from '../api/network';
import store from '../modules/store';
import Global from '../modules/Global';
import swiper from '../modules/swiper';
import dropDownRefresh from '../modules/refresh';

@Component({})
export default class Plug extends Vue {
    private swiperList = store.swiperList;

    private swipers = [
        {
            id: 'swiper-1',
            tip: '没有回路和底部圆点',
        }, {
            id: 'swiper-2',
            tip: '有回路和底部圆点'
        }, {
            id: 'swiper-3',
            tip: '垂直滚动'
        }, 
    ];
    
    private content = '';

    showAlert() {
        Global.openDialog({
            type: 'alert',
            content: '提示框',
        });
    }

    showConfirm() {
        Global.openDialog({
            type: 'confirm',
            content: '确定-取消提示框',
            cancel() {
                Global.openDialog({
                    type: 'alert',
                    content: '点击了取消',
                });
            }
        });
    }

    showToast() {
        Global.showToast('提示条~');
    }

    showLoading() {
        Global.openDialog({
            type: 'loading'
        });
        setTimeout(function() {
            Global.hideDialog();
        }, 3000);
    }

    postData() {
        Global.openDialog({
            type: 'loading'
        });
        // const patt = /github/;
        // if (patt.test(location.hostname)) {
            api.testGet(res => {
                Global.hideDialog();
                this.content = JSON.stringify(res);
                console.log('请求成功', res);
            }, err => {
                Global.hideDialog();
                console.log('请求错误', err);
            });
        // } else {
        //     api.testPost(res => {
        //         Global.hideDialog();
        //         this.content = JSON.stringify(res);
        //     }, err => {
        //         Global.hideDialog();
        //         console.log(err.message);
        //     })
        // }
    }
    
    copyText() {
        const el: any = this.$refs['text-box'];
        el.select();
        el.setSelectionRange(0, el.value.length);
        document.execCommand('copy');
        Global.openDialog({
            type: 'confirm',
            content: '复制成功，是否清空文本框数据？',
            cancelText: '不清空',
            confirm: () => {
                this.content = '';
            }
        });
    }

    mounted() {
        swiper({
            el: '#' + this.swipers[0].id,
            autoPaly: true
        });
        swiper({
            el: '#' + this.swipers[1].id,
            autoPaly: true,
            pagination: true,
            loop: true,
            interval: 4000
        });
        swiper({
            el: '#' + this.swipers[2].id,
            autoPaly: true,
            pagination: true,
            loop: true,
            vertical: true
        });
        const page: any = document.querySelector('.plug');
        const nav: any = document.querySelector('.nav');
        const refresh = dropDownRefresh({
            el: page,
            distance: 100,
            deviation: nav.clientHeight,
            loadIcon: `<div class="preloader">
                <div class="preloader-inner">
                    <div class="preloader-inner-gap"></div>
                    <div class="preloader-inner-left">
                        <div class="preloader-inner-half-circle"></div>
                    </div>
                    <div class="preloader-inner-right">
                        <div class="preloader-inner-half-circle"></div>
                    </div>
                </div>
            </div>`
        });
        refresh.onStart(() => {
            setTimeout(() => {
                refresh.end();
            }, 1800);
        });
    }
}
